*{
  margin: 0px;
  padding: 0px;
  /* font-size: 1.1vw; */
}
html{
  background-color: #282c34;
}
.App {
  text-align: center;
  color: white;
  background-color: #282c34;

}

.form-main{
  padding: 15px;
  display: flex;
  flex-direction: column;
}


.inputbox{
  width: 50%;
  margin: auto;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px !important;
}

.btn-main{
  /* width: 15% !important; */
  display: block;
  margin: auto !important;
}

.flexbox{
  display: flex;
}

.error{
  color:red;
}

.hide{
  display: none;
}

.red{
  color: red;
}

img{
  max-width: 100%;
  /* height: 40%; */
  object-fit: cover;
}

.card-deck{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card{
  width: 23%;
  margin: 10px;
  color: black;
  transition-duration: 0.6s;
  background-color: #dfe1e1;
}


.card:hover{
  cursor: pointer;
  transform: scale(1.04);
  box-shadow: 0px 0px 10px 1px white;
}

.next-prev-btn{
  display: flex;
  margin: 10px;
  padding: 15px;
}

@media screen and (max-width: 1000px) {
  .card{
    width: 45%;
    font-size: 1rem;
  }
  .inputbox{
    font-size: 1rem;
  }
}
h4{
  /* margin-top: 5px; */
  background-color: white;
  border-radius: 4px;
  padding: 5px;
  box-sizing: content-box;
  color: black;
}
@media screen and (max-width: 550px) {
  .card{
    width: 90%;
    /* font-size: 0.5rem; */
  }
}

@media screen and (max-width: 550px){
  .inputbox,.card{
    width: 100%;
  }
}

@media screen and (max-width: 300px){
  .next-prev-btn{
    flex-direction: column;
  }
}